                }
                var validateDbpwd = (rule, value, callback) => {
                  if (value === '') {
                    callback(new Error('请输入数据库密码'));
                  }else{
                    callback()
                  }
                }
                var validateDbuser = (rule, value, callback) => {
                  if (value === '') {
                    callback(new Error('请输入数据库用户名'));
                  }else{
                    callback()
                  }
                }
                var validateDbpre = (rule, value, callback) => {
                  if (value === '') {
                    callback(new Error('请输入数据库服务器地址'));
                  }else{
                    callback()
                  }
                }
                    return {
                      formindex:1,
                      dbinfo:'数据库初始化成功！',
