                          confirmButtonText: '知道了，去浏览网站',
                          showClose:false,
                          callback: action => {
                            window.location.href=_this.$store.state.baseurl+"index.php"
                          }
                        });
                        return false
                      }
                      if(response.data.code==200){

                        _this.$notify({
                          title: '提示',
                          message: '网站初始化信息创建成功',
                          type: 'success'
                        });
                        _this.$alert('恭喜你，网站安装完成，后台入口超级管理员前端登录后右上角头像下拉进去', '提示', {
                          confirmButtonText: '知道了，去浏览网站',
                          showClose:false,
                          callback: action => {

                           window.location.href=_this.$store.state.baseurl+"index.php"
                          }
                        });
                      }else{


                      }
                      _this.$message(response.data.msg)



                    }).catch(function (error) {
                      alert(error)
                    });
                  },
                  createuser(){
                    var _this = this;
                    _this.userinfo.dbpre=_this.ruleForm2.dbpre
                    var params = 'ajax='+encodeURIComponent(JSON.stringify(_this.userinfo));

                    _this.loading = true
                    axios.post(this.$store.state.baseurl + 'install/inapi.php?type=inituser',params).then(function (response) {
                      _this.loading = false
                      if(response.data.code==2001){
                        _this.$alert('网站已经安装过了', '提示', {
                          confirmButtonText: '知道了，去浏览网站',
                          showClose:false,
                          callback: action => {
                            window.location.href=_this.$store.state.baseurl+"index.php"
                          }
                        });
                        return false
                      }
                      if(response.data.code==200){
                         _this.formindex=2
                        _this.$notify({
                          title: '提示',
                          message: '超级管理员创建成功',
                          type: 'success'
                        });
                      }else{


                      }
                        _this.$message(response.data.msg)



                    }).catch(function (error) {
                      alert(error)
                    });
                  },
                initdb(){
                  var _this = this;
                  var params = 'ajax='+encodeURIComponent(JSON.stringify(_this.ruleForm2));

                  _this.loading = true
                  axios.post(this.$store.state.baseurl + 'install/inapi.php?type=initdb',params).then(function (response) {
                    _this.loading = false
                    if(response.data.code==2001){
                      _this.$alert('网站已经安装过了', '提示', {
                        confirmButtonText: '知道了，去浏览网站',
                        showClose:false,
                        callback: action => {
                          window.location.href=_this.$store.state.baseurl+"index.php"
                        }
                      });
                      return false
                    }
                   if(response.data.code==200){
                       _this.dbinitflag=true
                     _this.errdbinitflag=false
                     _this.dbinfo="数据库初始化成功"

                     _this.$alert('数据库初始化成功', '提示', {
                       confirmButtonText: '下一步，创建超级管理员',
                       showClose:false,
                       callback: action => {
                         _this.$store.state.step=3
                       }
                     });

                   }else{
                     _this.dbinitflag=false
                       _this.errdbinitflag=true
                     _this.dbinfo=response.data.msg

                   }



                    _this.fileitems=response.data

                  }).catch(function (error) {
                    alert(error)
                  });
                },
                getfileenv(){
                  var _this = this;
                  var params = '';
                  _this.loading = true
                  axios.post(this.$store.state.baseurl + 'install/inapi.php?type=getfileenv',params).then(function (response) {


                    _this.loading = false
                    if(response.data.code==2001){
                      _this.$alert('网站已经安装过了', '提示', {
                        confirmButtonText: '知道了，去浏览网站',
                        showClose:false,
                        callback: action => {
                          window.location.href=_this.$store.state.baseurl+"index.php"
                        }
                      });
                      return false
                    }
                    _this.fileitems=response.data

                   for(var _item in _this.fileitems){
                        console.log(_this.fileitems[_item])
                     if(_this.fileitems[_item]['status']==0||_this.fileitems[_item]['status']==-1){
                       _this.canfilepass=0
                     }
                   }
                    if(_this.canfilepass){
                      setTimeout(function(){
                        _this.showfilepass=1
                      },1000)

                      _this.$notify({
                        title: '成功',
                        message: '文件和目录权限检测通过',
                        type: 'success'
                      });
                      _this.$alert('环境以及文件目录权限检测通过', '提示', {
                        confirmButtonText: '准备数据库配置安装吧',
                        showClose:false,
                        callback: action => {
                          _this.$store.state.step=2
                          _this.lineindex=4
                        }
                      });
                    }else{
                      _this.$notify.error({
                        title: '错误',
                        message: '文件和目录权限检测不通过'
                      });
                    }


                    _this.lineindex=3
                  }).catch(function (error) {
                    alert(error)
                  });
                },
                  getenv(){
                    var _this = this;
                    var params = '';
                    _this.loading = true
                    axios.post(this.$store.state.baseurl + 'install/inapi.php?type=getenv',params).then(function (response) {


                      _this.loading = false
                      if(response.data.code==2001){
                        _this.$alert('网站已经安装过了', '提示', {
                          confirmButtonText: '知道了，去浏览网站',
                          showClose:false,
                          callback: action => {
                            window.location.href=_this.$store.state.baseurl+"index.php"
                          }
                        });
                        return false
                      }
                        _this.items=response.data
                     for(let i=0;i<_this.items.length;i++){

                          if(_this.items[i][3]==0){
                              _this.canpass=0
                          }
                     }

                      if(_this.canpass){
                         setTimeout(function(){
                           _this.showpass=1
                         },1000)

                        _this.$notify({
                          title: '成功',
                          message: '环境检测通过',
                          type: 'success'
                        });
                        setTimeout(function(){

                          _this.getfileenv()
                        },2000)
                      }else{
                        _this.$notify.error({
                          title: '错误',
                          message: '环境检测不通过'
                        });
                      }


                      _this.lineindex=2
                    }).catch(function (error) {
                      alert(error)
                    });
                  },
                gostep2(){
                    let target=this
                  target.$store.state.step=1
                  setTimeout(function(){
                    target.lineindex=1
                  },2000)
                  setTimeout(function(){

                    target.getenv()
                  },4000)
                }


                }
            ,mounted(){




                }
            }
        </script>
<style scoped="">
  .el-form,input,.el-input__inner{
    z-index: 0;
  }
  .demo-ruleForm{
    width: 500px;
    margin: 0 auto;
  }
  .el-table .cell,.el-table th>.cell{
    text-align: center;
  }
.el-icon-success
{
  color: #409EFF;
}
.el-icon-error{
  color: red;
}
.c_step {
    font-size: 40px;
    font-weight: 600;
    text-align: center;
    margin-top: 200px;

  }
  .button_op{
    margin-top: 30px;
  }
  .whatsns_pannel{
    margin-bottom: 170px;
    margin-top: 100px;
  }

  .license {

    height: auto;
    margin: 1rem auto;
    height: 400px;
    overflow: scroll;
  }
    .el-table .warning-row {
        background: oldlace;
    }

    .el-table .success-row {
        background: #f0f9eb;
    }
</style>



