import Vue from 'vue'
import Router from 'vue-router'
import Kehu from '@/components/kehu'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Kehu',
      component: Kehu
    },
      {
          path: '/kehu',
          name: 'Kehu',
          component: Kehu
      }

  ]
})



